import React from 'react'
import { Todoitem } from './Todoitem';

export const Todos = ({todos,onDelete,onUpdate}) => {

  /*
  const style={
    width:"35rem",
    padding:"0px 100px",
    backgroundColor:"lightblue"
  } */

  if (todos === null || todos.length===0 ) { console.log("error Todo: 0/null todo", todos); }
  else { console.log("fine Todo: List", todos); }

  return (
    <div className="container card m-5 p-5" style={{ width: "35rem", backgroundColor: "lightblue" }}>
      <h4 className=" text-center" >TODO List</h4>
      {(todos !== null) ?
        (todos.length!==0) ?
          todos.map((todo) => {
            return <Todoitem todo={todo} key={todo.sno} onDelete={onDelete} onUpdate={onUpdate} />
          })
          : <h3 className="card my-4 p-3" style={{ color: "purple", backgroundColor: "lightgreen" }}>
            No Todos to Display !Please wait some time ..., </h3>

        : <h3 className="card my-4 p-3" style={{ color: "purple", backgroundColor: "lightgreen" }}>
          No Todos to Display !Please wait some time ..., </h3>
      }


    </div>

  )
}
