import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'


export default function Header(props) {
  return (
    <div>
    <nav className="navbar navbar-expand-lg bg-light">
        <div className="container-fluid">
          <Link className="navbar-brand " style={{color:'purple'}} to="/">{props.heading}</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse " id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0 ">
              <li className="nav-item">
                <Link className="nav-link active btn" style={{color:'orange'}}aria-current="page" to="/home">Home</Link>
              </li> &ensp;
              <li className="nav-item ">
                <Link className="nav-link active btn" style={{color:'orange'}} to="/about">about</Link>
              </li>
              
            </ul>
   
             <form className="d-flex" role="search">
              <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
              <button className="btn btn-outline-success" type="submit">Search</button>
            </form>
          </div>
        </div>
      </nav>

    </div>
  )
}


//Header.defaultprop ={
    //title:  "My-Todo-list1",   
    //searchbar:true not linking to this function if want to print|^
    //{props.searchbar? "searchbar Available":"no searchbar Available"} }


Header.propTypes ={
    title: PropTypes.string,
    //searchBar: PropTypes.bool.isRequired
}
