import React from 'react'

export const Footer = () => {
  let Footerstyle={
    position:"absolute",
    top:"100vx",
    width:"100%"
  }
  return (
    <footer className="bg-dark text-light py-3 my-2" style={Footerstyle}>
    <p className="text-center ">
      Copyright &copy; Mytodolist.com
    </p>
    </footer>
  )
}
