import React from 'react'

export const Todoitem = ({todo, onDelete,onUpdate }) => {
  return (
    <div className=" card  p-3 m-1" style={{width:'30rem'}} >
      <h5 style={{color:'orange'}}>{todo.title} ({todo.sno})</h5>
      <p style={{color:'purple'}}>{todo.desc}</p>
      <ul>
      <button className= "btn btn-sm btn-danger" style={{width:'80px'}}  onClick={()=>{onDelete(todo)}}>Delete</button>
      &ensp;
      <button className= "btn btn-sm btn-danger" style={{width:'80px'}}  onClick={()=>{onUpdate(todo)}}>Update</button>
     </ul>
    </div>
  )
  
}
