import React, { useState } from 'react'

export const UpdateTodo = ({ updatetodo, todo: { sno, title, desc } }) => {

    let Usno=sno;
    console.log("const sno 1 value =", Usno);
    const [Utitle, setTitle] = useState(title);
    const [Udesc, setDesc] = useState(desc);



    const submit = (e) => {
        e.preventDefault();
        if (!Utitle || !Udesc) {
            alert("Title and Description cannot be empty");
        }
        else {
            const Updatedtodo = {
                sno: Usno,
                title: Utitle,
                desc: Udesc
            }

            updatetodo(Updatedtodo);
            Usno="";
            setTitle("");
            setDesc("");

        }
    }
    return (
        <form onSubmit={submit} className="card m-3 p-5" style={{ backgroundColor: "orange", width: '600px' }} >
            <div className="form-group">
                <label>Todo Sno</label>
                <input id="Usno" type="text" className="form-control" value={Usno} disabled />
            </div>
            <div className="form-group">
                <label>Todo Tittle</label>
                <input id="title" type="text" className="form-control" value={Utitle} onChange={(e) => setTitle(e.target.value)} 
                placeholder="Enter todo tittle"  />
            </div>
            <div className="form-group my-2">
                <label>Todo Description</label>
                <input id="desc" type="text" className="form-control" value={Udesc} onChange={(e) => setDesc(e.target.value)} 
                placeholder=" Enter todo Description" />
            </div>
            <button id="submit" type="submit" className="btn btn-primary " style={{ width: '120px' }} >updateTodo</button>
        </form>
    )
}
