import React, { useState } from 'react'

export const Addtodo = ({ addtodo, todos }) => {


    let sno;
    if (todos === null || todos.length === 0) {
        sno = 0;
    }
    else {
        sno = todos[todos.length - 1].sno + 1;
    }

    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");


    const submit = (e) => {
        e.preventDefault();
        if (!title || !desc) {

            alert("Sno, Title and Description cannot be empty");
        }
        else {
            addtodo(sno, title, desc);
            //setTitle("");
            //setDesc("");
            //setSno("");
        }
    }
    return (
        <form onSubmit={submit} className="card p-5" style={{ backgroundColor: "orange", width: '600px' }} >
            <div className="form-group">
                <label>Todo Sno</label>
                <input id="sno" title="sno" type="text" className="form-control" value={sno} disabled />
            </div>
            <div className="form-group">
                <label>Todo Tittle</label>
                <input id="title" type="text" className="form-control" value={title} onChange={(e) => setTitle(e.target.value)}
                    placeholder="Enter todo tittle" />
            </div>
            <div className="form-group my-2">
                <label>Todo Description</label>
                <input id="desc" type="text" className="form-control" value={desc} onChange={(e) => setDesc(e.target.value)}
                    placeholder=" Enter todo Description" />
            </div>
            <button id="submit" type="submit" className="btn btn-primary " style={{ width: '100px' }} >AddTodo</button>
        </form>
    )
}
