import React from 'react';
import './App.css';
import Header from './MyComponent/Header';
import { Todos } from './MyComponent/Todos';
import { Addtodo } from './MyComponent/Addtodo';
import { UpdateTodo } from './MyComponent/UpdateTodo';
import { About } from './MyComponent/About';
import { Footer } from './MyComponent/Footer';
import { useState } from 'react';
import { useEffect } from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";
function App() {

  // todos backup recovery----------------------------------------------------------------------------------

  let initTodo;
  if (localStorage.getItem === null) {
    initTodo = Array(0);
    console.log("boot time null initTodo", initTodo);

  }
  else {

    console.log("Local storage at boot time", localStorage.getItem)
    initTodo = JSON.parse(localStorage.getItem("todos"));
    console.log("boot time value of initTodo", initTodo);
    if (initTodo === null) {
      initTodo = Array(0);
    }
    console.log("boot time value of initTodo after [] this", initTodo);
  }

  //Decaring all variable will be used in app---------------------------------------------------------------

  const [todos, setTodos] = useState(initTodo);

  useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos]);

  const [state, setState] = useState(false);

  const [Utodo, setUtodo] = useState(null);


  //Declaring all function will be used in the app---------------------------------------------------------------

  const onDelete = (todo) => {
    console.log("i am onDelete of todo & deleting todo=", todo);

    setTodos(todos.filter((e) => {
      return e !== todo;
    }));
    console.log(" &&& now todos=", todos);

  }

  const updatetodo = ({ sno, title, desc }) => {
    console.log("i am updatetodofunc of todo", sno, title, desc);
    let found = 0;
    setTodos(todos.map((e) => {

      if (e.sno === sno) {
        e.sno = sno;
        e.title = title;
        e.desc = desc;
        found=1;
      };
      return e;
    }
    ));

    (found === 0) ?
      alert("The todo you want to update is not exist in todo list")
      : alert("Updated Successfully")

    console.log("updated mytodo is", sno, title, desc);
    console.log("init at updating mytodo", initTodo);
    setState(false);
    console.log("state after updatetod function run", state);
  };


  const onUpdate = (todo) => {
    setUtodo(todo);

    if (state === false) {
      setState(true);
    }
    else {
      setState(false);
    }
    console.log("i am onUpdate of todo & sending todo for Update=", todo);
  }

  const addtodo = (sno, title, desc,) => {
    console.log("i am addtodo of todo & value I got from form is", title, desc);
    const myTodo = {
      sno: sno,
      title: title,
      desc: desc
    }

    if (todos !== null) {
      setTodos([...todos, myTodo]);
    }
    else {

      setTodos([{ myTodo }]);
    }

    console.log(" Added new mytodo =", myTodo);
    console.log("init at Adding mytodo", initTodo);
  }


  //

  // ---------------------------FINALLY RETURING THE APP FUNCTION-------------------------------------------------------
  return (
    <div style={{ backgroundColor: "purple" }}>
      <Router>

        <Header heading="My-Todo-List" />

        <Routes>

          <Route path="/home" element={
            <div style={{ display: "flex" }} >
              <div className="m-5">
                <h3 className="m-5" style={{ color: 'white' }}>My React App</h3>

                <div>
                  {(state === false) ? <Addtodo addtodo={addtodo} todos={todos} />
                    : <UpdateTodo updatetodo={updatetodo} todo={Utodo} />}
                </div>


              </div>
              <div><Todos todos={todos} onDelete={onDelete} onUpdate={onUpdate} /> </div>
            </div>
          } />

          <Route exact path="/about" element={<About />} />

        </Routes>

        <Footer />

      </Router>
    </div>
  );
}

export default App;
